
import { defineComponent } from 'vue'
import { DateTimeFormatOptions } from '../types'

interface Activity {
  start: string
  finish: string
  activity: string
}
interface Data {
  start: Date | null
  activity: string
  activities: Activity[]
}

const timeOptions: DateTimeFormatOptions = {
  hour12: false,
  hour: `2-digit`,
  minute: `2-digit`,
}
export default defineComponent({
  name: `Home`,
  components: {},
  data: (): Data => ({
    start: null,
    activity: ``,
    activities: [],
  }),
  computed: {},
  watch: {},
  methods: {
    submitActivity() {
      const startTime = this.start?.toLocaleTimeString([], timeOptions)
      const endTime = new Date().toLocaleTimeString([], timeOptions)
      const newActivity = {
        start: startTime,
        finish: endTime,
        activity: this.activity,
        timeTook: Math.floor(((new Date()).getTime() - this.start) / 1000 / 60),
      }
      this.activities = [newActivity, ...this.activities]
      this.start = new Date()
      this.activity = ``
    },
    startDay() {
      this.start = new Date()
    },
  },
})
